<template>
  <div class="login">
    <RXin />
    <div class="login-box">
      <div class="login-title-box">
        <div class="login-title">找回密码</div>
        <div class="login-sub-title">Find the password</div>
      </div>

      <div class="form-box">
        <van-field
          left-icon="contact"
          v-model="userRegisterInfos.phone"
          label="手机号"
          placeholder="输入手机号(11位)"
        />
        <van-field
          left-icon="newspaper-o"
          @click-right-icon="toggle"
          v-model="userRegisterInfos.password"
          :type="istype ? 'text' : 'password'"
          label="新密码"
          placeholder="输入密码(6~16位)"
          :right-icon="istype ? 'eye-o' : 'closed-eye'"
        />
        <div @click="back" class="zh" style="color: white">
          已有账号,立即登录
        </div>
        <div class="btn-box">
          <van-button
            @click="retrievePassword"
            round
            block
            color="linear-gradient(to bottom, skyblue 0%, pink 100%)"
            >登&nbsp;&nbsp;&nbsp;录</van-button
          >
          <van-button
            class="register-btn"
            type="default"
            round
            block
            color="linear-gradient(to bottom, skyblue 0%, pink 100%)"
            >返&nbsp;&nbsp;&nbsp;回</van-button
          >
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import "../assets/less/login.less";
import RXin from "../components/RXin.vue";
export default {
  name: "Zhaohui",
  data() {
    return {
      userRegisterInfos: {
        phone: "",
        password: "",
      },

      istype: false,
    };
  },

  components: {
    RXin,
  },

  methods: {
    toggle() {
      this.istype = !this.istype;
    },

    back() {
      this.$router.push({ name: "Login" });
    },

    //找回密码
    retrievePassword() {

      let yz = {
        phone: {
          value: this.userRegisterInfos.phone,
          reg: /^1[3-9]\d{9}$/,
          error: "手机号码格式不正确",
        },

        password: {
          value: this.userRegisterInfos.password,
          reg: /^[A-Za-z]\w{5,15}$/,
          error: "密码支持字母数字_组合首字符为字母",
        },
      };

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0,
      });

      this.axios({
        method: "POST",
        url: this.baseUrl + "/retrievePassword",

        data: {
          appkey: this.appkey,
          phone: this.userRegisterInfos.phone,
          password: this.userRegisterInfos.password,
        },
      })
        .then((res) => {
          
          this.$toast.clear();
          if (res.data.code == "L001") {
            this.iszh = false;
            for (let key in this.userRegisterInfos) {
              this.userRegisterInfos[key] = "";
            }
            return this.$router.push({ name: "Login" });
          }
          this.$toast({
            message: "找回密码成功",
            forbidClick: true,
            duration: 3000,
          });
        })
        .catch((err) => {
          this.$toast.clear();
          
        });
    },
  },
};
</script>

<style lang="less" scoped>
.zh {
  float: right;
  margin-top: 15px;
}
</style>